import { IconButton } from '@chakra-ui/button'
import { Input } from '@chakra-ui/input'
import { Box } from '@chakra-ui/layout'
import React, { useCallback, useEffect, useMemo, useState } from 'react'
import useSWR from 'swr'
import { FiChevronLeft, FiChevronRight } from 'react-icons/fi'
import { useNumberInput } from '@chakra-ui/number-input'

const ChevButton = props => (
  <IconButton
    variant="unstyled"
    rounded="full"
    bgGradient="linear(to-r, custom.orange, custom.yellow)"
    bgColor="white"
    color="white"
    boxShadow="md"
    fontSize="1.75em"
    width="1.125em"
    minW="1.125em"
    height="1.125em"
    display="flex"
    px="0.25em"
    py="0.25em"
    _disabled={{
      bgGradient: "linear(to-r, custom.orangeAlpha, custom.yellowAlpha)",
      opacity: 1,
      cursor: 'not-allowed',
    }}
    _hover={{
      opacity: 1,
    }}
    {...props}
  />
)

const YearSelector = ({ onChange }) => {
  const { data = [] } = useSWR('/yearsList')
  const handleChange = useCallback(v => {
    onChange(data[data.length - 1 - v])
  }, [data, onChange])
  const {
    getInputProps,
    getIncrementButtonProps,
    getDecrementButtonProps,
  } = useNumberInput({
    step: 1,
    defaultValue: 0,
    min: 0,
    max: Math.max(0, data.length - 1),
    onChange: handleChange
  })

  const inc = getIncrementButtonProps()
  const dec = getDecrementButtonProps()
  const input = getInputProps({ isReadOnly: true })
  return (
    <Box position="relative" width="7em" bg="white" rounded="full" border="2xp solid white" boxShadow="inner">
      <Input
        width="100%"
        textAlign="center"
        variant="unstyled"
        py="0.125em"
        fontSize="1.25em"
        letterSpacing="wider"
        value={data[data.length - 1 - input.value]}
        isReadOnly
        // {...input}
      />
      <Box position="absolute" left="-0.75em" top="50%" transform="translate(0, -50%)">
        <ChevButton icon={<FiChevronLeft />} {...inc} />
      </Box>
      <Box position="absolute" right="-0.75em" top="50%" transform="translate(0, -50%)">
        <ChevButton icon={<FiChevronRight />} {...dec} />
      </Box>
    </Box>
  )
}

export default YearSelector
