import { Box, Flex } from '@chakra-ui/layout'
import React from 'react'

const TypeToggler = ({ value, onChange, options }) => {
  return (
    <Flex position="relative" rounded="full" bg="white" boxShadow="inner" width="10em" fontSize="1.125em">
      <Box
        position="absolute"
        transition="all 500ms"
        bgGradient="linear(to-r, custom.orange, custom.yellow)"
        width="52.5%"
        height="120%"
        left="0"
        top="0"
        transform={`translate(${value ? 90 : -2.5}%, -10%)`}
        rounded="full"
        boxShadow="md"
      />
      {options.map((label, i) => (
        <Box
          width="100%"
          key={i}
          px="0.5em"
          py="0.25em"
          color={value === i ? 'black' : 'custom.gray'}
          fontWeight={value === i ? 700 : 500}
          position="relative"
          transition="all 500ms"
          letterSpacing="widest"
          textAlign="center"
          onClick={() => onChange(i)}
          cursor="pointer"
        >
          {label}
        </Box>
      ))}
    </Flex>
  )
}

export default TypeToggler
