import React, { useState } from 'react'
import { HStack, Stack } from '@chakra-ui/layout'

import Container from '../components/Container'
import Box from '../components/Box'
import Flex from '../components/Flex'
import Text from '../components/Text'
import BackgroundImage from '../components/BackgroundImage'

import Header from './Header';
import BudgetBar from './BudgetBar'
import RevenueTree from './RevenueTree'
import SpendingTree from './SpendingTree'

const types = [
  '市縣',
  '鄉鎮市',
]

const HomePage = () => {
  const [isSub, setIsSub] = useState(0)
  const [year, setYear] = useState(() => new Date().getFullYear() - 1911)
  const type = isSub ? 'B' : 'A'
  const typeLabel = types[+isSub]
  return (
    <Container py="2em">
      <Header
        typeLabel={typeLabel}
        type={isSub}
        onTypeChange={setIsSub}
        typeOptions={types}
        year={year}
        onYearChange={setYear}
      />
      <HStack spacing="2em" my="3em" alignItems="flex-start">
        <Box flex="1">
          <BudgetBar type={type} typeLabel={typeLabel} year={year} />
        </Box>
        <Stack spacing="1em" flex="1">
          <RevenueTree type={type} typeLabel={typeLabel} year={year} />
          <SpendingTree type={type} typeLabel={typeLabel} year={year} />
        </Stack>
      </HStack>
    </Container>
  )
}

export default HomePage
