import React, { useMemo } from 'react'
import useSWR from 'swr'
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  LabelList,
  Rectangle,
} from 'recharts';
import { format } from 'd3-format'

import useLabel from '../api/useLabel'
import Box from '../components/Box'
import Text from '../components/Text'
import Flex from '../components/Flex'
import Card from '../components/Card'
import theme from '../components/ThemeProvider/theme'
import { Spinner } from '@chakra-ui/spinner';

const legend = [
  {
    label: '歲入',
    bg: 'custom.lightCyan'
  },
  {
    label: '歲出',
    bg: 'custom.lightBlue'
  },
]

const fontSize = 18
const f = format(',d')

const RenderCustomAxisTick = ({ x, y, payload, width }) => {
  const w = fontSize * 4.5
  const h = fontSize * 2.125
  return (
    <g transform={`translate(${x - width - fontSize * 1.375}, ${y - fontSize * 1.125})`}>
      <Rectangle
        radius={8}
        width={w}
        height={h}
        fill="url(#Gradient1)"
        opacity="0.175"
      />
      <Rectangle
        radius={8}
        width={w}
        height={h}
        fill="none"
        stroke={theme.colors.custom.textBlue}
      />
      <text
        x={w / 2}
        y={h / 2 + fontSize * 0.35}
        fontSize={fontSize}
        fill={theme.colors.custom.textBlue}
        textAnchor="middle"
      >
        {payload.value}
      </text>
    </g>
  );
};

const BudgetBar = ({ type, year, typeLabel }) => {
  const label = useLabel('budgetTitle')
  const { data } = useSWR(`/data/cities?year=${year}&type=${type}`)
  const chartData = useMemo(() => data ? data.map(({ name, revenue, spending }) => ({
    name,
    歲出: spending,
    歲入: revenue
  })) : [], [data])
  return (
    <Card
      title={`各${typeLabel}${label}`}
      width="100%"
      titleRight={
        <Flex>
          {legend.map(({ label, bg }, i) => (
            <Flex alignItems="center" ml={i && '1.5em'} key={i}>
              <Box mr="0.375em" width="2.5em" py="0.5em" bg={bg} />
              <Text color="custom.labelBlue">{label}</Text>
            </Flex>
          ))}
        </Flex>
      }
    >
      {data ? (
        <BarChart
          layout="vertical"
          width={500}
          height={1350}
          min={0}
          barGap={0}
          data={chartData}
          margin={{
            top: 0,
            right: 20,
            left: 36,
            bottom: 0,
          }}
        >
          <defs>
            <linearGradient id="Gradient1" x1="0" x2="0" y1="0" y2="1">
              <stop class="stop1" offset="0%" stop-color="rgba(0,114,159,0)"/>
              <stop class="stop2" offset="100%" stop-color="rgba(0,114,159,1)"/>
            </linearGradient>
          </defs>
          <XAxis
            type="number"
            tick={false}
            tickLine={false}
            axisLine={false}
            strokeWidth={0}
            padding={{ right: 240 }}
          />
          <YAxis
            tickLine={false}
            axisLine={false}
            dataKey="name"
            type="category"
            tick={<RenderCustomAxisTick />}
          />
          <Bar dataKey="歲入" barSize={25} fill={theme.colors.custom.lightCyan}>
            <LabelList
              dataKey="歲入"
              position="right"
              fill={theme.colors.custom.cyan}
              formatter={f}
            />
          </Bar>
          <Bar dataKey="歲出" barSize={25} fill={theme.colors.custom.lightBlue}>
            <LabelList
              dataKey="歲出"
              position="right"
              fill={theme.colors.custom.blue}
              formatter={f}
            />
          </Bar>
        </BarChart>
      ) : <Spinner colorScheme="green" />}
    </Card>
  )
}

export default BudgetBar
