import { Spinner } from '@chakra-ui/spinner'
import React from 'react'
import useSWR from 'swr'
import useLabel from '../api/useLabel'
import Card from '../components/Card'
import TreeMap from './Charts/TreeMap'

const SpendingTree = ({ type, year, typeLabel }) => {
  const label = useLabel('spendingTitle')
  const { data } = useSWR(`/data/spending?year=${year}&type=${type}`)

  return (
    <Card title={label} hightlight={`全${typeLabel}`}>
      {data ? <TreeMap data={data} colorScheme="green" valueColor="darkGreen" /> : <Spinner colorScheme="green" />}
    </Card>
  )
}

export default SpendingTree
