import React, { useMemo } from 'react'
import { Treemap, Tooltip } from 'recharts';
import { maxBy, minBy, sortBy } from 'lodash';
import { scaleLinear } from 'd3-scale'
import { format } from 'd3-format'

import Box from '../../components/Box'
import Text from '../../components/Text'

const f = format(',d')

const CustomTooltip = ({ active, payload, label, valueColor }) => {
  // console.log(active, props)
  if (active && payload && payload.length) {
    return (
      <Box bg="rgba(255, 255, 255, 0.9)" borderRadius="0.5em" p="0.25em 0.5em">
        <Text fontSize="1.125em">{payload[0].payload.name}</Text>
        <Text fontSize="1.625em" color={`custom.${valueColor}`}>{`${f(payload[0].value)}`}</Text>
      </Box>
    );
  }
  return null;
};

const CustomizedContent = ({
  depth,
  x,
  y,
  width,
  height,
  name,
  colorScale,
  value,
}) => {
  return (
    <g>
      <rect
        x={x}
        y={y}
        width={width}
        height={height}
        style={{
          fill: colorScale(value),
          stroke: '#fff',
          strokeWidth: 2,
        }}
      />
      {depth === 1 ? (
        <text x={x + width / 2} y={y + height / 2 + 7} textAnchor="middle" fill="#fff" fontSize={18}>
          {width < name.length * 20 ? '...' : name}
        </text>
      ) : null}
    </g>
  );
}


const colors = {
  green: ['#A7E6E3', '#2F857E'],
  blue: ['#CDE7FA', '#0D5A8C']
}

const TreeMap = ({ data, colorScheme, valueColor }) => {
  const treeData = useMemo(() => data ? sortBy(data.map(({ name, value }) => (
    {
      name,
      value: +value
    }
  )), ['value']).reverse() : [], [data])
  const extent = useMemo(() => {
    const min = minBy(treeData, 'value')
    const max = maxBy(treeData, 'value')
    return [min?.value || 0, max?.value || 1]
  }, [treeData])
  const colorScale = useMemo(() =>
    scaleLinear()
      .domain(extent)
      .range(colors[colorScheme])
    , [extent, colorScheme])
  return (
    <Treemap
      width={530}
      height={600}
      data={treeData}
      ratio={4 / 3}
      dataKey="value"
      animationDuration={500}
      content={<CustomizedContent colorScale={colorScale} />}
    >
      <Tooltip valueColor={valueColor} content={<CustomTooltip />} />
    </Treemap>
  )
}

export default TreeMap
