import { Box, Heading, HStack, Text } from '@chakra-ui/layout'
import React from 'react'
import BackgroundImage from './BackgroundImage'

import arrow from './arrow.svg'

const Card = ({
  title,
  hightlight,
  titleRight,
  children,
  ...props
}) => (
  <Box
    rounded="2em"
    p="2em"
    boxShadow="lg"
    bg="white"
    {...props}
  >
    <HStack
      pb="1em"
      mb="1.5em"
      position="relative"
      spacing="0.625em"
      _before={{
        content: '""',
        display: 'block',
        position: 'absolute',
        left: 0,
        right: 0,
        bottom: 0,
        height: '1px',
        bgImage: 'linear-gradient(to right, #238CC5, #B7A5D1)',
      }}
    >
      <BackgroundImage width="1.375em" ratio={1} src={arrow} />
      <Heading color="custom.textBlue" fontSize="1.5em" letterSpacing="widest">
        {hightlight && <Text mr="0.25em" as="span" color="custom.textOrange">{hightlight}</Text>}
        {title}
      </Heading>
      <Box flex="1" />
      {titleRight}
    </HStack>
    {children}
  </Box>
)

export default Card
