import React from 'react'
import { Heading, HStack, Flex } from "@chakra-ui/react";

import UpdatedAt from './UpdatedAt';
import useLabel from '../api/useLabel';
import TypeToggler from './TypeToggler';
import YearSelector from './YearSelector';

const Header = ({
  type,
  typeLabel,
  onTypeChange,
  typeOptions,
  year,
  onYearChange,
  ...props
}) => {
  const label = useLabel('headTitle')
  return (
    <Flex
      alignItems="flex-end"
      borderBottom="1px solid white"
      pb="1.5em"
      {...props}
    >
      <HStack flex={1} spacing="1.5em">
        <Heading as="h1" color="white" letterSpacing="widest" fontSize="1.75em" minW={`${label.length + 5}em`}>
          各{typeLabel}{label}
        </Heading>
        <YearSelector value={year} onChange={onYearChange} />
        <TypeToggler value={type} options={typeOptions} onChange={onTypeChange} />
      </HStack>
      <UpdatedAt />
    </Flex>
  )
}

export default Header
